<template>
  <div class="homePage">
      <div class="container">
          <h2 class="title">闫杰.简历</h2>
          <div class="avatar">
              <img src="./avatar.png" class="avatar-img" width="50px" height="50px" alt="avater">
              <p class="avatar-age">Age:23</p>
              <p class="avatar-works">Job Hunting:frontend</p>
          </div>
          <div class="footer-img"></div>
          <div class="pull-down" @click="nextPages('nav')">
              <a class="pull-icon" href="javascript:;"><i class="iconfont icon-icon-copy"></i></a>
          </div>
      </div>
      <div class="nav" id="nav">
          <v-navigation></v-navigation>
          <div style="height:78px;width:100%; position:absolute;top:0;z-index:-100"></div>
      </div>
      <div class="basic" id="basic">
          <v-basicInfor></v-basicInfor>
      </div>
      <div class="pro" id="pro">
        <v-pro-exp></v-pro-exp>
      </div>
      <div class="skill" id="skill" ref="skill">
        <v-skills></v-skills>
      </div>
      <div class="exp" id="exp">
        <v-experience></v-experience>
      </div>
      <div class="est" id="est">
        <v-estimate></v-estimate>
      </div>
      <div class="con" id="con">
        <v-contact></v-contact>
      </div>
  </div>
</template>


<script>
import navigation from "../navigation/navigation";
import basicInfor from "../basicInfor/basicInfor";
import skills from "../skills/skills";
import experience from "../experience/experience";
import estimate from "../estimate/estimate";
import pro_exp from "../pro_exp/pro_exp";
import contact from "../contact/contact";
import { moveStart } from "../../common/js/smoothPage";
export default {
  created() {
    this.$root.$on("another", this.nextPages);
  },
  methods: {
    getObj(stringId) {
      return document.getElementById(stringId);
    },
    nextPages(stringId, number) {
      let obj = this.getObj(stringId);
      let iScroll = obj.getBoundingClientRect().top;
      if (number) {
        iScroll =
          (document.documentElement.scrollHeight ||
            document.body.scrollHeight) -
          (document.documentElement.clientHeight ||
            document.body.clientHeight) -
          78;
      } else {
        iScroll =
          document.documentElement.scrollTop +
          document.body.scrollTop +
          iScroll -
          78;
      }

      moveStart.scrollPage(document.body, iScroll, 10);
    }
  },
  components: {
    "v-navigation": navigation,
    "v-basicInfor": basicInfor,
    "v-pro-exp": pro_exp,
    "v-skills": skills,
    "v-experience": experience,
    "v-estimate": estimate,
    "v-contact": contact
  }
};
</script>


<style lang='scss' scoped>
.homePage {
  height: 100%;
  width: 100%;
  .container {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    .title {
      text-align: center;
      font-size: 20px;
      margin: 10px;
    }
    .avatar {
      margin: 80px auto auto 20px;
      padding: 10px;
      .avatar-img {
        padding: 15px;
        border: green 1px solid;
        border-radius: 4px;
      }
      .avatar-works,
      .avatar-age {
        font-size: 12px;
      }
    }
    .footer-img {
      position: absolute;
      bottom: 10px;
      right: 0;
      width: 110px;
      height: 70px;
      background: url(./bear.jpg) no-repeat;
      background-size: contain;
    }
    .pull-down {
      position: absolute;
      bottom: 25px;
      margin: 0 auto;
      width: 100%;
      text-align: center;
      .pull-icon {
        display: inline-block;
        padding: 15px;
        border: solid 2px #fff;
        border-radius: 50%;
      }
      .iconfont {
        font-size: 25px;
        color: red;
      }
      .icon-icon-copy {
        animation: pullDown 2s steps(10) infinite;
      }
      @keyframes pullDown {
        0% {
          transform: scale(1);
          color: red;
        }
        50% {
          transform: scale(2);
          color: #fc848a;
        }
        100% {
          transform: scale(1);
          color: red;
        }
      }
    }
  }
  #nav {
    position: relative;
    height: 78px;
    transition: all 2s linear;
  }
}
</style>


