// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import iScroll from './global_base'
Vue.config.productionTip = false
Vue.use(iScroll)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  // 这个将模块挂载到某个实例当中去，引用外层文件的index.html
  router,
  // 这个是之router里面的index.js文件
  template: '<App/>',
  // 这个显示app文件的东西
  components: { App }
  // 这个是router文件夹下的index.js文件下的路由显示模块
})



