(function(){
    function getStyle(obj, attr) {
        return obj.currentStyle ? obj.currentStyle[attr] : getComputedStyle(obj, false)[attr];
    }
    function scrollPage(obj, number, time) {
        clearInterval(obj.timer);
        obj.timer = setInterval(() => {
            let iSpeed = (parseInt(number - obj.scrollTop)) / 8;
            iSpeed = iSpeed > 0 ? Math.ceil(iSpeed) : Math.floor(iSpeed);
            if (obj.scrollTop == number) {
                clearInterval(obj.timer);
            }
            obj.scrollTop = obj.scrollTop + iSpeed
        }, time)
    }
    function getViewport() {
        if (document.compatMode == "CSS1Compat") {
            return document.body;
        }
        return document.documentElement;
    }
    window.moveStart = {};
    window.moveStart['getStyle'] = getStyle;
    window.moveStart['scrollPage'] = scrollPage;
    window.moveStart['getViewport'] = getViewport;
}())
export { moveStart }


