<template>
<div class="basicInfor">
    <div class="container">
        <div class="title">
            <h5 class="title-content">基本 <span>资料</span></h5>
        </div>
        <span class="split-line"></span>
        <div class="content clear-float">
            <div class="line" v-for="(item,index) in line_mount" :key="index">
                <div class="item-content" v-for="(itemContent,index) in item" :key="index">
                    <div class="iconStyle"><i class="iconfont" :class="itemContent.iconType"></i></div>
                    <h3 class="content-title">{{itemContent.contentTitle}}</h3>
                    <span class="content-one">{{itemContent.contentOne}}</span>
                    <span class="content-two">{{itemContent.contentTwo}}</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>


<script>
export default {
  data() {
    return {
      line_mount: [
        [
          {
            iconType: "icon-gerenxinxi",
            contentTitle: "个人信息",
            contentOne: "性别:男",
            contentTwo: "年龄:23 籍贯:河南"
          },
          {
            iconType: "icon-xueli1",
            contentTitle: "专业学历",
            contentOne: "专业:软件工程",
            contentTwo: "学历：大学本科"
          }
        ],

        [
          {
            iconType: "icon-biyexuexiao-new",
            contentTitle: "毕业学校",
            contentOne: "毕业学校:郑州师范学院",
            contentTwo: "学习技能：前端开发与设计"
          },

          {
            iconType: "icon-zhengshu1",
            contentTitle: "荣誉证书",
            contentOne: "CET4",
            contentTwo: "中国计算机省级设计大赛第三名"
          }
        ]
      ]
    };
  }
};
</script>

<style lang='scss' scoped>
.basicInfor {
  .container {
    .title {
      margin:12px 0 12px 25px;
      .title-content{
        font-size:18px;
        margin:0;
        span {
          color: orange;
        }
      }
    }
    .split-line {
      display: block;
      margin-left: 50px;
      height:2px;
      background: #1889c5;
    }
    .content .line {
      margin-top: 12px;
      display: flex;
      .item-content {
        flex: 1;
        text-align: center;
        margin-top: 14px;
        .iconStyle {
          margin: 0 auto;
          display: table;
          border-radius: 50%;
          width: 70px;
          height: 70px;
          background: #42474b;
          transition: all 0.5s linear;
          .iconfont {
            display: inline-block;
            font-size: 28px;
            display: table-cell;
            vertical-align: middle;
            color: #fff;
          }
        }
        &:hover .iconStyle {
          transform: scale(1.3);
          .iconfont {
            color: #f4d03f;
          }
        }
        .content-title {
          color: orange;
          font-size: 14px;
        }
        .content-one,
        .content-two {
          display: block;
          font-size: 12px;
        }
      }
    }
  }
}
</style>


