<template>
<div class="navbar">
    <div class="first-line">
        <p class="even"  @click="anotherPage('basic')">基本资料</p>
        <p class="odd"  @click="anotherPage('pro')">项目经验</p>
        <p class="even" @click="anotherPage('skill')">专业技能</p>
        <p class="odd" @click="anotherPage('exp')">工作经历</p>
    </div>
    <div class="second-line">
        <p class="even" @click="anotherPage('est',1)">自我评价</p>
        <p class="odd" @click="anotherPage('est',1)">联系方式</p>
    </div>
</div>
</template>


<script>
export default {
  methods:{
    anotherPage(stringId,number){
      this.$root.$emit('another',stringId,number);
    }
  }
};
</script>


<style lang='scss' scoped>
@mixin odd-even {
  &.even {
    background: #373e40;
    color: #fff;
    user-select: none;
  }
  &.odd {
    background: #a2b4ba;
    color: #fff;
    user-select: none;
    
  }
}
@mixin vertical-middle {
  height: 36px;
  line-height: 36px;
}
.navbar {
  // position: fixed;
  &.fixed {
    position: fixed;
    z-index: 111;
     box-shadow: 22px 1px 24px black;
  }
  width: 100%;
  background: #fff;
  top: 0;
  .first-line {
    display: flex;
    p {
      cursor: pointer;
      text-align: center;
      flex: 1;
      margin: 0px 4px 2px 0;
      border-radius: 3px;
      @include vertical-middle;
      @include odd-even;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .second-line {
    display: flex;
    p {
      text-align: center;
      flex: 1;
      margin: 4px 2px 0 0;
      border-radius: 3px;
      @include vertical-middle;
      @include odd-even;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
</style>


