      background: #26b1b6;
    }
    .description {
      text-align: center;
      font-size: 12px;
      margin: 12px 7px 0px 7px;
    }
    .content {
      .content-list {
        .content-li {
          margin-right: 7px;
          .headline {
            font-size: 14px;
            margin: 0;
            border-left: 2px solid #22b573;
            .head-title {
              float: left;
              margin: 0 0 0 10px;
            }
            .time {
              float: right;
            }
          }
          .para {
            margin-left: 20px;
            padding: 4px;
