<template>
<div class="pro-exp">
        <div class="container">
          <div class="title">
            <h4 class="title-content">项目 <span>经验</span></h4>
          </div>
          <span class="split-line"></span>
          <p class="description" v-text="des">{{des}}</p>
          <div class="content ">
            <ul class="clear-float">
                <li v-for="(item,index) in listItem" :key="index" class="title-class" :class="{active:activeIndex===index}" @click="_active($event)" ref="option">{{item.name}}</li>
            </ul>
            <ul class="clear-float placeImg">
                <li v-for="(itemImg,index) in listImg" :key="index" class="imgClass" :class="[itemImg.proType]" ref="itemImg">
                    <img v-bind:src="itemImg.src" alt="programer" >
                </li>
            </ul>
            </div>
        </div>
</div>
</template>


<script>
export default {
  data() {
    return {
      des: "主要涉及电商，金融，家装领域，包括PC端，手机端，微信端，移动APP端等等，主要技术是HTML+CSS+JS",
      listItem: [
        { name: "全部", type: "imgClass" },
        { name: "PC端", type: "PC" },
        { name: "移动端", type: "mobile" },
        { name: "webApp", type: "webApp" }
      ],
      listImg: [
        {
          src: "../static/img/works/01.png",
          proType: "PC"
        },
        {
          src: "../static/img/works/01.png",
          proType: "PC"
        },
        {
          src: "../static/img/works/01.png",
          proType: "mobile"
        },
        {
          src: "../static/img/works/01.png",
          proType: "PC"
        },
        {
          src: "../static/img/works/01.png",
          proType: "webApp"
        },
        {
          src: "../static/img/works/01.png",
          proType: "mobile"
        }
      ],
      activeIndex: 0
    };
  },
  methods: {
    _active(e) {
      let eleTarget = e.target;
      for (let i = 0; i < this.listItem.length; i++) {
        if (eleTarget == this.$refs.option[i]) {
          this.activeIndex = i;
          switch (i) {
            case 0:
              this._classify(this.listItem[i].type);
              break;
            case 1:
              this._classify(this.listItem[i].type);
              break;
            case 2:
              this._classify(this.listItem[i].type);
              break;
            case 3:
              this._classify(this.listItem[i].type);
          }
        }
      }
    },
    _classify(lei) {
      for (let i = 0; i < this.listImg.length; i++) {
        if (this.$refs.itemImg[i].classList.contains(lei)) {
          if (lei == this.listItem[0].type) {
            this.$refs.itemImg[i].style.transform = "scale(1)";
          } else {
            this.$refs.itemImg[i].style.transform = "scale(1.1)";
          }
          this.$refs.itemImg[i].style.opacity = 1;
        } else {
          this.$refs.itemImg[i].style.transform = "scale(0.5)";
          this.$refs.itemImg[i].style.opacity = 0.5;
        }
      }
    }
  }
};
</script>


<style lang='scss' scoped>
.pro-exp {
  .container {
    transition: all 2s linear;
    .title {
      margin: 24px 0 12px 25px;
      .title-content {
        font-size: 18px;
        margin: 0;
        span {
          color: #22b573;
        }
      }
    }
    .split-line {
      display: block;
      margin-left: 50px;
      height: 2px;
      background: #22b573;
    }
    .description {
      text-align: center;
      font-size: 12px;
      margin: 12px 7px 0px 7px;
    }
    .content {
      .clear-float {
        padding: 0;
        text-align: center;
        .active {
          background: #22b573;
          color: #fff;
        }
        .title-class {
          font-size: 12px;
          display: inline-block;
          margin-right: 8px;
          padding: 4px 16px;
          border-radius: 4px;
          cursor: pointer;
          &:last-child {
            margin-right: 0;
          }
        }
      }
      .placeImg {
        .imgClass {
          display: inline-block;
          margin-right: 15px;
          margin-top: 20px;
          border-radius: 5px;
          transition: all 0.5s linear;
        }
      }
    }
  }
}
</style>


