<template>
  <div class="estimate">
    <div class="container">
      <div class="title">
        <h4 class="title-content">自我
          <span>评价</span>
        </h4>
      </div>
      <span class="split-line"></span>
      <div class="content">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div v-for="(item,index) in commentList" :key="index" class="swiper-slide">
              <p v-html="item.content" class="para">
              </p>
            </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import Swiper from "../../../static/js/swiper.min.js";
export default {
  data() {
    return {
      commentList: [
        {
          content: `热爱学习，良好的团队精神，能迅速融入新的团队.<br>
                易和人相处，拥有良好的亲和力`
        },
        {
          content: `热爱学习，良好的团队精神，能迅速融入新的团队
                <br> 易和人相处，拥有良好的亲和力`
        },
        {
          content: `热爱学习，良好的团队精神，能迅速融入新的团队
                <br> 易和人相处，拥有良好的亲和力`
        }
      ]
    };
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      direction: "horizontal",
      autoplay: 3000,
      speed:300,
      loop: true,
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination"
      },
     
    });
  }
};
</script>


<style lang='scss' scoped>
@import "../../../static/css/swiper.min.css";
.estimate {
  .container {
    transition: all 2s linear;
    .title {
      margin: 24px 0 12px 25px;
      .title-content {
        font-size: 18px;
        margin: 0;
        span {
          color: #5A4446;
        }
      }
    }
    .split-line {
      display: block;
      margin-left: 50px;
      height: 2px;
      background: #5A4446;
    }
    .content {
      .swiper-container {
        height: 200px;
        margin: 12px 7px 0 50px;
        text-align: center;
        .swiper-slide {
          display: table;
          .para {
            display: table-cell;
            vertical-align: middle;
            font-size: 12px;
          }
        }
      }
    }
  }
}
</style>


