<template>
  <div class="skills">
       <div class="container">
        <div class="title">
            <h5 class="title-content">专业 <span>技能</span></h5>
        </div>
        <span class="split-line"></span>
        <div class="rotate ">
            <div class="content font">
                <div v-for="(item,index) in skill_list" :key="index" class="skill-panel clear-float">
                  <i class="iconfont" :class="item.iconType"></i>
                  <div class="skill-content">
                  <h5 class="skill-title">{{item.title}}</h5>
                  <p class="skill-para">{{item.para}}</p>
                   </div>
                  </div>
            </div>
        <div class="right"></div>
        </div>
        <!-- <div class="content">
            <div v-for="(item,index) in skill_list" :key="index" class="skill-panel clear-float">
              <i class="iconfont" :class="item.iconType"></i>
              <div class="skill-content">
                <h5 class="skill-title">{{item.title}}</h5>
                <p class="skill-para">{{item.para}}</p>
              </div>
            </div>
        </div> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      skill_list: [
        {
          iconType: "icon-mubiaoyuyan",
          title: "语言技术栈",
          para: `能够熟练使用H5、CSS3，JS原生编程; 
熟练的使用webpack，gulp等前端构建和打包工具; 
能够熟练运用less，sass,等一些预编译语言进行开发.`
        },
        {
          iconType: "icon-kuangjia",
          title: "框架技术栈",
          para: `熟练使用JQuery.js库,vue.js库进行开发; 
熟练并使用BootStrap、amaze.ui、element.ui、mint.ui进行开发`
        },
        {
          iconType: "icon-lei_zhishidianbiaoqian",
          title: "其他知识点",
          para: `对node.js有一定的了解; 
        拥有PS扎实的基础;
       对网站性能优化有充分的认识和处理办法; 
      实现W3C语义化开发`
        }
      ]
    };
  }
};
</script>

<style lang='scss' scoped>
.skills {
  .container {
    transition: all 2s linear;
    perspective: 18000px;
    .title {
      margin: 24px 0 12px 25px;
      .title-content {
        font-size: 18px;
        margin: 0;
        span {
          color: #9c58b6;
        }
      }
    }
    .split-line {
      display: block;
      margin-left: 50px;
      height: 2px;
      background: #9c58b6;
    }
    .rotate {
      position: relative;
      opacity: 0;
      &.isRotate {
        transform: rotateX(40deg);
        animation: rotate 2s ease-in-out forwards;
        transform-style: preserve-3d;
        opacity: 1;
        @keyframes rotate {
          0% {
            transform: rotatey(-120deg);
          }
          100% {
            transform: rotatey(0deg);
          }
        }
      }

      & > div {
        backface-visibility: hidden;
      }
      .font {
        margin-left: 25px;
        transform: translatez(100px);
        .skill-panel {
          display: flex;
          &:first-child {
            margin-top: 12px;
          }
          .iconfont {
            flex: 0 0 30px;
            font-size: 18px;
            color: #9c58b6;
          }
          .skill-content {
            flex: 1;
            margin-right: 7px;
            .skill-title {
              margin: 0;
              color: #9c58b6;
            }
            .skill-para {
              text-align: justify;
              font-size: 14px;
            }
          }
        }
      }
      .right {
        position: absolute;
        top: 0px;
        width: 290px;
        height: 280px;
        background: linear-gradient(
          to left,
          rgba(156, 88, 182, 0.6),
          #fff 100%
        );
        transform: rotatey(130deg) translatez(110px);
      }
    }
    // .content {
    //   position: relative;
    //   margin-left: 25px;
    //   .skill-panel {
    //     display: flex;
    //     &:first-child {
    //       margin-top: 12px;
    //     }
    //     .iconfont {
    //       flex: 0 0 30px;
    //       font-size: 18px;
    //       color: #9c58b6;
    //     }
    //     .skill-content {
    //       flex: 1;
    //       margin-right: 7px;
    //       .skill-title {
    //         margin: 0;
    //         color: #9c58b6;
    //       }
    //       .skill-para {
    //         text-align: justify;
    //         font-size: 14px;
    //       }
    //     }
    //   }
    // }
  }
}
</style>


